import * as types from './types';
var initReusableSaga = function () {
    return {
        type: types.ACTION_TYPE.INIT_REUSABLE_SAGA,
    };
};
var initReusableAction = function (payload) {
    return {
        type: types.ACTION_TYPE.INIT_REUSABLE,
        payload: payload,
    };
};
/**
 * 更新排序
 */
var updateReusable = function (payload) {
    return {
        type: types.ACTION_TYPE.UPDATE_REUSABLE,
        payload: payload,
    };
};
/**
 * 更新node
 */
var updateReusableItem = function (payload) {
    return {
        type: types.ACTION_TYPE.UPDATE_REUSABLE_ITEM,
        payload: payload,
    };
};
export { initReusableSaga, initReusableAction, updateReusable, updateReusableItem };
